@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.wrapper {
  background-image: url('./images/background.png');
  background-size: auto 100%;
  height: calc(100% - 40px);
  padding: 20px;

  .inner-wrapper {
    border-radius: 10px;
    border: 2px solid #86602c;
    background-color: rgba(antiquewhite, 0.9);
    height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;

    .item-container {
      flex: 1;
      overflow-y: auto;
    }

    .menu {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: antiquewhite;
      z-index: 1;
      border-radius: 10px;

      .close-btn {
        position: absolute;
        top: 18px;
        right: 12px;
        z-index: 1;

        img {
          width: 24px;
          height: 24px;
        }
      }

      .categories-container {
        margin: 0;
        width: 100%;
        max-height: 100%;
        position: absolute;
        top: 50%;
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
        align-items: center;
      }

      p {
        max-width: 60%;
        text-align: center;
        padding: 10px;
      }

      .category-item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px 0px;
        font-size: 14px;
        margin: auto;
        max-width: 73%;
        flex-wrap: wrap;
        gap: 6px;

        .line {
          height: 1px;
          background-color: black;
          flex: 1;
        }

        .surf-icon {
          height: 18px;
          width: 18px;
        }

        .left-surf-icon {
          justify-self: start;
          transform: rotate(45deg);
        }

        .right-surf-icon {
          transform: rotate(225deg);
        }
      }

      img {
        width: 30px;
        height: 30px;
      }
    }

    .languages {
      .languages-container {
        margin: 0;
        width: 100%;
        max-height: 100%;
        position: absolute;
        top: 50%;
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
        align-items: center;

        .language-item {
          display: flex;
          align-items: center;
          padding: 10px 0px;
          font-size: 20px;
          margin: auto;
          max-width: 50%;
          gap: 20px;
        }

        img {
          width: 40px;
          height: 40px;
        }

      }
    }

    header {
      display: flex;
      align-items: center;
      padding: 10px;
      justify-content: space-between;

      img {
        width: 32px;
        height: 32px;
      }

      .title {
        font-size: 30px;
        color: black;
        margin: 0;
      }
    }

    .actions {
      display: flex;
      align-items: center;
      padding: 10px 30px;
      justify-content: space-between;

      img {
        width: 32px;
        height: 32px;
      }

      img.left-arrow {
        transform: rotate(180deg);
      }
    }

    .category {
      animation: fadeInAnimation ease 1s;
      animation-iteration-count: 1;
      animation-fill-mode: forwards;
    }

    .category-title {
      text-align: center;
      font-size: 30px;
      margin: 15px 0;
    }

    .product {
      font-size: 20px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 5px 10px;
      margin: 10px 0;
      gap: 8px;

      .ingredients {
        font-size: 16px;
        color: blue;
      }

      p {
        margin: 0;
      }
    }
  }
}